import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
// import components
import * as Yup from "yup";
import { useNavigate } from "react-router-dom";
import { Formik } from "formik";

import { userActions, alertActions } from "src/actions";
import Cookies from "universal-cookie";
// MUI stuff
import {
  Box,
  Container,
  Button,
  TextField,
  InputAdornment,
} from "@mui/material";
import makeStyles from "@mui/styles/makeStyles";
import RemoveRedEyeOutlinedIcon from "@mui/icons-material/RemoveRedEyeOutlined";
import VisibilityOffOutlinedIcon from "@mui/icons-material/VisibilityOffOutlined";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100vw",
    height: "calc(100vh - 72px)",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: theme.palette.grayscale[0],
    overflow: "hidden",
  },

  // loginBox
  loginBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    maxWidth: 504,
    width: "100vw",
    padding: "0 16px",
  },

  loginTextField: {
    marginTop: 4,
    marginBottom: theme.spacing(3),
    "& .MuiInputBase-root": {
      height: 45,
      borderRadius: theme.spacing(1),
    },

    "& .MuiOutlinedInput-input": {
      padding: theme.spacing(1.5),
    },
  },
  ActivateBtn: {
    minWidth: 280,
    height: 49,
    borderRadius: theme.spacing(1.5),
    background: theme.palette.nemoMint[400],
    color: theme.palette.nemoMint[800],
    ...theme.typography.h5,
    "&:disabled": {
      background: theme.palette.nemoMint[400],
      color: theme.palette.nemoMint[800],
      opacity: 0.5,
    },
    "&:hover": {
      background: theme.palette.nemoMint[400],
      color: theme.palette.nemoMint[800],
      opacity: 0.5,
    },
  },

  appleBtn: {
    width: "100%",
    height: 45,
    backgroundColor: theme.palette.grayscale[1000],
    color: theme.palette.grayscale[0],
    borderRadius: theme.spacing(1),
    border: 0,
    marginBottom: theme.spacing(1),
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: "0 16px",
    cursor: "pointer",
  },
  emailText: {
    ...theme.typography.h5,
    width: "100%",
    paddingBottom: 40,
  },
  inputClass: {
    "&.Mui-focused": {
      border: "2px solid #555",
      "& .MuiOutlinedInput-notchedOutline": {
        border: "none",
      },
    },
  },
}));

function Email(props) {
  const classes = useStyles();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const cookies = new Cookies();
  const params = new URLSearchParams(window.location.search);
  let from;
  const alertState = useSelector((state) => state.alert);
  const user = cookies.get("token");
  const [isPassword, setIsPassword] = useState(false);
  useEffect(() => {
    dispatch(alertActions.clear());
  }, []);

  useEffect(() => {
    if (params.get("from") === null) {
      from = "";
    } else {
      from = params.get("from");
    }

    if (alertState.message === " " && user) {
      navigate(`/${from}`);
    } else if (alertState.message) {
      alert(alertState.message);
    }

    if (alertState.message === "   .") {
      navigate("/sign-up");
    }
  }, [alertState]);
  return (
    <Box className={classes.root}>
      <Box className={classes.loginBox}>
        <Container maxWidth="md">
          <Box className={classes.emailText}> </Box>
          <Formik
            initialValues={{
              email: "",
              password: "",
            }}
            validationSchema={Yup.object().shape({
              email: Yup.string()
                .email("   .")
                .max(255)
                .required(" "),
              password: Yup.string()
                .required(" ")
                .max(16, " 16  ")
                .min(8, " 8  ")
                .matches(
                  /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
                  ", ,    . (8-16)"
                ),
            })}
            onSubmit={async (values, { setSubmitting }) => {
              await dispatch(userActions.register(values));
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
              control,
            }) => (
              <form name="form" onSubmit={handleSubmit}>
                <Box sx={{ typography: "h6", color: "grayscale.600" }}>
                  
                </Box>
                <TextField
                  className={classes.loginTextField}
                  error={errors.email && touched.email}
                  helperText={
                    errors.email && touched.email ? errors.email : null
                  }
                  fullWidth
                  margin="normal"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  type="test"
                  value={values.email}
                  variant="outlined"
                  placeholder="nemo@nemo.co.kr"
                  InputProps={{
                    maxLength: 40,
                    className: classes.inputClass,
                  }}
                />
                <Box sx={{ typography: "h6", color: "grayscale.600" }}>
                  
                </Box>
                <TextField
                  className={classes.loginTextField}
                  error={errors.password && touched.password}
                  helperText={
                    errors.password && touched.password ? errors.password : null
                  }
                  fullWidth
                  margin="normal"
                  name="password"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  type={isPassword ? "text" : "password"}
                  value={values.password}
                  variant="outlined"
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        {isPassword ? (
                          <VisibilityOffOutlinedIcon
                            sx={{ cursor: "pointer" }}
                            onClick={() => setIsPassword(false)}
                          />
                        ) : (
                          <RemoveRedEyeOutlinedIcon
                            sx={{ cursor: "pointer" }}
                            onClick={() => setIsPassword(true)}
                          />
                        )}
                      </InputAdornment>
                    ),
                    maxLength: 40,
                    placeholder: "8-16  ",
                    className: classes.inputClass,
                  }}
                />
                <Box my={1}>
                  <Button
                    fullWidth
                    size="large"
                    type="submit"
                    disabled={Boolean(errors.password) || Boolean(errors.email)}
                    variant="contained"
                    className={classes.ActivateBtn}
                  >
                    
                  </Button>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-around",
                    typography: "button",
                  }}
                >
                  <Box
                    onClick={() => navigate("/sign-up")}
                    sx={{ cursor: "pointer" }}
                  >
                     
                  </Box>
                  <Box> </Box>
                  <Box> </Box>
                </Box>
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Box>
  );
}

export default Email;
