import React, { useEffect, useState } from "react";
import { Controller } from "react-hook-form";

import { Box, FormControlLabel, Checkbox } from "@mui/material";

const CheckBoxComponent = (props) => {
  const [labelContent, setLabelContent] = useState();

  useEffect(() => {
    labelText();
  }, []);

  const labelText = () => {
    if (props.label === "") setLabelContent(" ");
    else if (props.label === "") setLabelContent("");
    else if (props.label === "") setLabelContent("  ");
    else if (props.label === "") setLabelContent(" ");
  };

  return (
    <Box>
      <FormControlLabel
        control={
          <Controller
            name={props.name}
            control={props.control}
            rules={{ required: true }}
            render={({ field: { value, ...field } }) => (
              <Checkbox
                {...field}
                checkedIcon={
                  <img
                    src="/static/images/products/checkBox.png"
                    alt="checkBox"
                  />
                }
                icon={
                  <img
                    src="/static/images/products/checkBoxRound.png"
                    alt="checkBoxRound"
                  />
                }
                sx={{
                  height: 32,
                  color: "grayscale.200",
                  "&.Mui-checked": {
                    color: "nemoMint.400",
                  },
                }}
                checked={props.optionFiler.some((e) => e.label === props.label)}
                onClick={(e) =>
                  props.handleCheckBox(e, props.id, props.label, props.title)
                }
              />
            )}
          />
        }
        label={
          <Box>
            {props.label}
            <Box sx={{ typography: "body2", color: "grayscale.700" }}>
              {labelContent}
            </Box>
          </Box>
        }
      />
    </Box>
  );
};

export default CheckBoxComponent;
